
.info-stats {
	display: flex;

	&__item {
		margin: 0 50px 0 0;

		@include sm {
			width: 188px; } }

	&__title {
		@include bold;
		font-size: 24px;
		line-height: 1.28;
		color: var(--clr-green);
		margin: 0 0 8px;

		@include sm {
			font-size: 18px;
			margin: 0 0 4px; } }



	&__desc {
		@include regular;
		font-size: 16px;
		line-height: 1.64;
		margin: 0;

		@include sm {
			font-size: 16px; } } }
