.citizenship-info {
	display: flex;

	@include lg {
		flex-direction: column; }

	&__tip {
		margin: 100px 0 0; }

	&__card {
		flex: 1 1 50%;
		min-width: 524px;
		margin: 0 65px 0 0;

		@include xl {
			min-width: 364px; }

		@include lg {
			margin: 0 0 40px; }

		@include med {
			position: relative;
			min-width: 300px;
			margin: 0 -10px 30px; }

		.visa-card {
			width: 364px;
			margin: 0 auto;

			@include med {
				width: 300px; } } } }


