@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin regular {
	font-family: var(--gothamPro);
	font-weight: 400; }

@mixin thin {
	font-family: var(--gothamPro);
	font-weight: 300; }

@mixin medium {
	font-family: var(--gothamPro);
	font-weight: 500; }

@mixin bold {
	font-family: var(--gothamPro);
	font-weight: 700; }

@mixin xl {
  @include r(1280) {
    @content; } }

@mixin lg {
  @include r(1024) {
    @content; } }

@mixin med {
  @include r(768) {
    @content; } }

@mixin sm {
  @include r(576) {
    @content; } }
@mixin xs {
  @include r(480) {
    @content; } }
